	padding: 2rem;
	border-radius: 10px;
	box-shadow: 2px 5px 10px 2px rgb(0 0 0 / 10%);
}
.card-single span:first-child{
	color: #f8e80c;
}
.card-single span:last-child{
	font-size: 2rem;
}
.card-single small{
	color: grey;
}
.card-single:hover{
	background: #008ea1;
	color: #fff;
}
.composant{
	margin-top: 3.5rem;
	display: grid;
	grid-gap: 2rem;
	grid-template-columns: 69% auto;
}
.case{
	background: #fff;
	border-radius: 10px;
}
.header-case, .body-case{
	padding: .5rem;
}
.header-case{
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.header-case button{
	background: #008ea1;
	border-radius: 10px;
	color: #fff;
	font-size: .8rem;
	padding: .5rem 1rem;
	border: none;
	cursor: pointer;
}
.btn{
	background: #008ea1;
	border-radius: 10px;
	color: #fff;
	font-size: .8rem;
	padding: .5rem 1rem;
	border: none;
	margin-left: 5px;
	cursor: pointer;
	margin-bottom: 1rem;
}
table{
	border-collapse: collapse;
}
