	height: 15px;
	width: 15px;
	margin-right: 1rem;
	border-radius: 50%; 
}
.status-produit.color-one{
	background:  #ec1c24;
}
.status-produit.color-two{
	background:  #fff200;
}
.status-produit.color-three{
