}
.bar5{
	height: 36%;
	background:  #ec1c24;
}
.bar6{
	height: 98%;
	background:  #00a8f3;
}
.legende{
	background: #fff;
	border-radius: 10px;
}
.legende h4{
	padding: 1rem;
}
td .evolution{
	display: inline-block;
	height: 15px;
	width: 15px;
	margin-right: 1rem;
	border-radius: 50%;
}
.evolution.color-one{
 	background: #ec1c24; 
}
.evolution.color-two{
 	background: #00a8f3; 
}
.txt-deco{
	padding: 1rem;
	font-weight: 500;
	color: grey;
}

#menu:checked ~ .sidebar{
	width: 60px;
}
#menu:checked ~ .sidebar .sidebar-brand, h2 span:last-child,
#menu:checked ~ .sidebar li a span:last-child {
	display: none;
}
#menu:checked ~ .content{
	margin-left: 60px;
}
#menu:checked ~ .content header{
	width: calc(100% - 60px);
	left: 60px;
}

.calendar{
	padding-top: 2rem;
}
ul {
	list-style-type: none;
}


/* mois header */
.mois-annee {
  padding: 70px 25px;
  width: 100%;
  background: #008ea1;
  text-align: center;
}

/* mois list */
.mois-annee ul {
  margin: 0;
  padding: 0;
}

.mois-annee ul li {
  color: white;
  font-size: 20px;
  text-transform: uppercase;
  letter-spacing: 3px;
}

/* Previous button inside mois header */
.mois-annee .prev {
  float: left;
  padding-top: 10px;
}

/* Next button */
.mois-annee .next {
  float: right;
  padding-top: 10px;
}


.serviceImg{
	width: 50px;
	height: 50px;
}

/* mois (Mon-Sun) */
.mois {
  margin: 0;
  padding: 10px 0;
  background-color:#ddd;
}

.mois li {
  display: inline-block;
  width: 13.6%;
  color: #666;
  text-align: center;
}

/* jours (1-31) */
.jours {
  padding: 10px 0;
  background: #eee;
  margin: 0;
}

.jours li {
  list-style-type: none;
  display: inline-block;
  width: 13.6%;
  text-align: center;
  margin-bottom: 5px;
  font-size:12px;
  color: #777;
}

/* Highlight the "current" day */
.jours li .active {
  padding: 5px;
  background: #008ea1;
  color: white !important
}

.parent{
    width: 100%;
}


@media only screen and (max-width: 960px){
	.cards {
    grid-template-columns: repeat(3, 1fr);
    
}
	.recent-grid{
		grid-template-columns: 60% 40%;
	}
}
@media only screen and (max-width: 768px){
	.cards {
		grid-template-columns: 100%;
	}
	.composant{
		grid-template-columns: repeat(1, 1fr);
	}
	.search-wrapper{
		display: none;
	}
	 .sidebar{
		left: -100% !important;
	}
	header p{
		display: flex;
		align-items: center;
	}
	header p label{
		display: flex;
		padding-right: 0rem;
		margin-right: 1rem;
		height: 40px;
		width: 40px;
		border-radius: 50%;
		color: #fff;
		align-items: center;
		justify-content: center !important;
		background: #008ea1;
	}
	header p span{
		text-align: center;
		padding-right: 0rem;
	}
	.content{
		width: 100%;
		margin-left: 0rem !important;

	}
	header{
		width: 100% !important;
		left: 0 !important;
	}
	#menu:checked ~ .sidebar{
			left: 0 !important;
			z-index: 100;
			width: 45%;
	}
	#menu:checked ~ .sidebar li a {
		padding-left: 1rem;
	}
	#menu:checked ~ .sidebar .sidebar-brand,
	#menu:checked ~ .sidebar li {
		padding-left: 2rem;
		text-align: left;
	}
	#menu:checked ~ .sidebar .sidebar-brand, h2 span:last-child,
	#menu:checked ~ .sidebar li a span:last-child {
		display: inline;
	}
	#menu:checked ~ .content{
		margin-left: 0rem !important;
	}

}



@media only screen and (max-width: 560px){
	 .cards{
		grid-template-columns: 100%;
	}
}
@media only screen and (max-width: 1200px){
	 .sidebar{
		width: 15%;
	}
	.sidebar .sidebar-brand, h2 span:last-child,
	.sidebar li a span:last-child {
		display: none;
	}
	 .content{
        margin-left: 14%;
	}
	 .content header{
		width: calc(100% - 60px);
		left: 60px;
	}

}


.addServices{
    width: 60%;
    margin: auto;
}
