.About-banner{
    padding:100px 0px 300px;
    position: relative;
    background:url("./foodbanner.jpg");
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center center;
    background-size: cover;
    height:70%;
    overflow: hidden;
}

